<template>
  <div class="login">
    <div class="content">
      <div class="contenter">
        <div class="login_left">
          <div class="logo">
            <span class="iconfont icon-lihansongjing theme-text"></span>
          </div>
          <div class="titles">发现经典之美</div>
        </div>
        <div class="login_right">
          <div class="rcode">
            <!-- <a href="javascript:void(0)" @click="isShow=false" v-if="isShow==true">
              <img src="../../assets/img/ewm.png" class="erweima">
            </a>
            <a href="javascript:void(0)" v-else>
              <img src="../../assets/img/phone.png" class="erweima">
            </a>-->
            <div class="validation">
              <h3 v-if="isShow==true">手机验证码登录</h3>
              <h3 v-else>{{tipInfo}}</h3>
            </div>
            <div v-if="this.isShow==true">
              <div class="phone">
                <el-input placeholder="请输入手机号" v-model="phoneNum"></el-input>
                <div class="send">
                  <a
                    href="javascript:void(0)"
                    class="getCode light-text"
                    v-show="show"
                    @click="sendCode"
                  >获取验证码</a>
                  <a
                    href="javascript:void(0)"
                    class="getCode light-text"
                    v-show="!show"
                  >{{count+'后重新发送'}}</a>
                </div>
              </div>
              <div class="code">
                <el-input placeholder="请输入验证码" v-model="codeNum"></el-input>
              </div>
              <button @click="login">登录</button>
            </div>
            <div class="cosignhow" v-else>
              <QrcodeVue :value="qrText" size="100" class="qrCode"/>
            </div>
            <!-- <div class="three">
              <p class="load-s">其他方式登录</p>
            </div>-->
            <!-- <div class="wechat">
              <span class="iconfont icon-wechat assistant-text" v-if="isShow==true"></span>
              <span class="iconfont icon-phone assistant-text" v-else></span>
            </div>-->
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import QrcodeVue from "qrcode.vue";
export default {
  data() {
    return {
      qrText: "",
      isShow: false,
      phoneNum: "",
      codeNum: "",
      show: true,
      count: "",
      timer: null,
      tipInfo:'扫码登录'
    };
  },
  mounted() {
    let APIURL = "";
    if (process.env.NODE_ENV === "development") {
      APIURL = "";
    } else {
      APIURL = "https://dock.lihan.ltd";
    }
    this.API = APIURL;

    this.scanLogin();
  },
  methods: {
    // sendCode() {
    //   var that = this;
    //   if (that.phoneNum !== "") {
    //     const TIME_COUNT = 60;
    //     if (!this.timer) {
    //       this.count = TIME_COUNT;
    //       this.show = false;
    //       this.timer = setInterval(() => {
    //         this.getCode();
    //         if (this.count > 0 && this.count <= TIME_COUNT) {
    //           this.count--;
    //         } else {
    //           this.show = true;
    //           clearInterval(this.timer);
    //           this.timer = null;
    //         }
    //       }, 1000);
    //     }
    //   } else {
    //     this.$alert("手机号不能为空！", {
    //       confirmButtonText: "确定"
    //     });
    //   }
    // },
    // getCode() {
    //   var that = this;
    //   var url = this.APIURL + "classic/usr/user/captcha";
    //   let data = {
    //     area: +86,
    //     phone: that.phoneNum
    //   };
    //   this.$axios
    //     .post(url, data)
    //     .then(res => {
    //       if (res.data.result == "success") {
    //         console.log(res.data);
    //         return;
    //       }
    //     })
    //     .catch(err => {});
    // },
    // // 登录鉴权
    // login() {
    //   var that = this;
    //   var url = this.APIURL + "classic/usr/user/login";
    //   let data = {
    //     area: +86,
    //     phone: that.phoneNum,
    //     captcha: that.codeNum,
    //     platform: "phone"
    //   };
    //   this.$axios.post(url, data).then(res => {
    //     console.log(res.data);
    //     if (res.data.method == "register") {
    //       this.registered();
    //     }
    //   });
    // },
    // // 注册
    // registered() {
    //   var that = this;
    //   var url = this.APIURL + "classic/usr/user/register";
    //   let data = {
    //     area: +86,
    //     phone: that.phoneNum,
    //     captcha: that.codeNum
    //   };
    //   this.$axios.post(url, data).then(res => {
    //     console.log(res.data);
    //   });
    // }
    scanLogin() {
      var _this = this;
      var url = this.API + "/wander/classic/user/qr?type=pc";
      this.$axios
        .get(url)
        .then(({ data }) => {
          console.log(data);
          if (data.result == "suc") {
            if (data.msg == "success") {
              //  _this.imgUrl = data.data.url;
              _this.qrText = data.data.raw;
              _this.runComet();
            }
            if (data.msg == "login") {
              this.$router.push({ path: "/index" });
              _this.getUserInfo();
            }
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    runComet() {
      var url = this.API + "/wander/classic/user/tick";
      var _this = this;
      this.$axios
        .get(url)
        .then(({ data }) => {
          console.log(data);
          if (data.result === "suc") {
            if (data.msg == "success" || data.msg == "login") {
              _this.tipInfo = "登录成功";
              this.$router.push({ path: "/index" });
              localStorage.setItem(
                "sessionId",
                JSON.stringify(data.data.session)
              );
              localStorage.setItem(
                "key",
                JSON.stringify(data.data.key)
              );
              this.$store.commit("setSessionKey", data.data.session);
              this.$store.commit("setKey",data.data.key)
              _this.getUserInfo();
            }
            if (data.msg == "continue") {
              _this.runComet();
            }
          } else if (data.result === "register") {
            _this.qrText = "https://m.lihan.ltd";
            _this.tipInfo = "请扫码注册";
          } else {
           _this.runComet();
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    intervalQuery() {
      var url = this.APIURL + "/wander/classic/user/tick";
      var _this = this;
      this.$axios
        .get(url)
        .then(({ data }) => {
          console.log(data);
          if (data.msg == "success" || data.msg == "login") {
            _this.tipInfo = "登录成功";
            clearTimeout(_this.interva);
            _this.getUserInfo();
            setTimeout(function() {
              this.$router.push({ path: "/index" });
            }, 1000);
          }
          if (data.msg == "continue") {
            this.interva = setTimeout(function() {
              _this.intervalQuery();
            }, 5000);
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    setInterval() {
      var vm = this;
      this.interva = setTimeout(function() {
        vm.intervalQuery();
      }, 5000);
    },
    getUserInfo() {
      var url = this.APIURL + "/classic/usr/user/info";
      var _this = this;
      this.$axios
        .post(url, "", { headers: { SessionKey: this.SessionKey,key:this.key } })
        .then(({ data }) => {
          console.log(data);
          if (data.result == "suc") {
            localStorage.setItem("userInfo", JSON.stringify(data.data));
            this.$store.commit("setUserInfo", data.data);
          }
        });
    }
  },
  computed: {
    SessionKey() {
      return this.$store.state.SessionKey;
    },
    key() {
      return this.$store.state.key;
    }
  },
  watch: {
    SessionKey() {
      this.scanLogin();
    }
  },
  components: {
    QrcodeVue
  }
};
</script>
<style lang="scss">
.login {
