          width: 88px;
          height: 88px;
          opacity: 1;
          top: 0;
          right: 0;
        }
        .validation {
          padding: 56px 120px 40px;
          h3 {
            opacity: 1;
            font-size: 20px;
            font-family: Microsoft YaHei Regular;
            color: rgba(0, 0, 0, 1);
            margin: 0;
            letter-spacing: 2px;
            text-align: center;
          }
        }
        .phone {
          margin: 0 auto 20px;
